<template>
  <div class="game">

  </div>
</template>

<script>
import * as PIXI from 'pixi.js'
import * as TRAVISO from 'traviso.js'

export default {
  name: "WorldComponent",
  mounted() {
    this.setup()
  },
  methods: {
    setup() {
      this.$store.commit('setIsLoading', true)
      let pixiRoot = new PIXI.Application({ resizeTo: window, backgroundColor: 0x6BACDE })
      let gameContainer = document.querySelectorAll('.game')[0]
      gameContainer.appendChild(pixiRoot.view)

      let instanceConfig = {
        mapDataPath: "assets/map/map.json", // the path to the json file that defines map data, required
        assetsToLoad: ["assets/map/grassTile.png", "assets/map/house.png"] // array of paths to the assets that are desired to be loaded by traviso, no need to use if assets are already loaded to PIXI cache, default null
      };

      let engine = new TRAVISO.getEngineInstance(instanceConfig);
      pixiRoot.stage.addChild(engine);
      //this.$store.commit('setIsLoading', false)
      //this.$store.commit('setIsPlaying', true)
    }
  }
}
</script>

<style lang="scss" scoped>
.game {
  position: relative;
  width: 100vw;
  height: 100vh;
  z-index: 1;
}
</style>